import React from "react";

export function GetContent(props){
    if(props.title === "paytm: Helping hand")
        return (<div>
            <p>
                Through the <strong>'helping hands'</strong> portal of paytm you can directly pay to foundations and charities like
                <li>AIC NCore Developmental Impact Foundation</li>
                <li>Alamelu Charitable Foundation</li>
                <li>Bal Raksha Bharat - Save the Children</li>
                <li>CRY - Child Rights and You</li>
                <li>Child Heart Foundation</li>
                <li>Gaon Connection Foundation</li>
                <li>Give Foundation</li>
                <li>Habitat for Humanity India</li>
                <li>Healthy Aging India</li>
                <li>Kailash Satyarthi Children's Foundation</li>
                <li>Karnataka CM Relief Fund Natural Calamity</li>
                <li>Kerala CM's Distress Relief Fund</li>
                <li>Maharashtra Chief Minister's Relief Fund</li>
                <li>Meghalaya Chief Minister's Relief Fund</li>
                <li>Milaan - Be the change</li>
            </p>
        </div>);
    else if(props.title === "Donatekart")
        return (<p>DonateKart is a unique platform that allows organizations to create campaigns for their ongoing projects.<br/> Through DonateKart, NGOs display the products they are in immediate need of.<br/>Donors can choose the organization that they wish to support and donate the products they want to.<br/>The process of buying these products is extremely convenient and is the same as shopping online.<br/>Similar to online shopping once you make the payment, the products will be delivered to the organization you support either at the end of the campaign (or) once they reach the target.<br/>You will always be able to track your donation and Campaigns will keep updating the donors about the utilisation of products.</p>);
    else if(props.title === "GiveIndia")
        return (<p>GiveIndia is a giving platform that allows you to support a cause of your choice from the NGOs that have been evaluated for transparency and credibility.

            GiveIndia is India's largest and most trustworthy giving platform for donors.<br/>With contributions of over Rs. 3 billion made to our 200+ vetted and trusted nonprofits, our giving community of 1M+ donors have impacted lives of millions of people across 23 states in India.<br/>We tell you exactly where your money went and also give you proof of it through donation reports.

We have reviewed hundreds of nonprofit organizations from all over India to identify and list the ones that meet our GiveAssured criteria.<br/>They work for a host of causes ranging from child welfare and education to disability, poverty, and women's empowerment and many others.</p>);
    else if(props.title === "Milaap")
        return (<p>Raise funds online for yourself, loved ones, charities, and more.
            <br/>Milaap addresses the many challenges of giving (such as difficulty in discovering the right causes, trust deficit and overall donor apathy). <br/>They share stories of (pre-qualified) individuals and groups with a network of its trusted community and partners, who either specialize in providing and managing microloans to their local communities, or can connect with the cause to help with a donation.

As of April 2018, the platform has raised nearly INR 330 Cr to support 100,000 causes, impacting millions of lives across the country.<br/>Currently, donations for medical emergencies form an overwhelming majority. The platform, a majorly utilitarian one is constantly striving to take the concept of online fundraising to more and more Indians<br/>Backed by the increasing penetration of social media and the popularization of digital payments, Milaap has quickly become India’s most preferred platform to raise funds for personal causes.
            </p>);
    else if(props.title === "UNICEF: for every child")
        return (<p> <a href="http://unicef.in/">UNICEF</a> is fully committed to working with the Government of India to ensure that each child born in this country gets the best start in life, thrives and develops to his or her full potential.
            <br/>The organisation began its work in India in 1949 with three staff members and established an office in Delhi three years later. Currently, it advocates for the rights of India’s children in 16 states.<br/>


        </p>);
    else if(props.title === "UNHCR: UN refugee agency")
        return (<p>UNHCR, the UN Refugee Agency, is a global organization dedicated to saving lives, protecting rights and building a better future for refugees, forcibly displaced communities and stateless people.<br/>
            Our primary purpose at UNHCR is to safeguard the rights and well-being of people who have been forced to flee.<br/>Together with partners and communities, we work to ensure that everybody has the right to seek asylum and find safe refuge in another country.<br/>We also strive to secure lasting solutions.

For over half a century, UNHCR has helped millions of people to restart their lives.<br/>They include refugees, returnees, stateless people, the internally displaced and asylum-seekers.<br/>Our protection, shelter, health and education has been crucial, healing broken pasts and building brighter futures.
        </p>);
}


