import React from "react";
import { Button, Card, Image } from "semantic-ui-react";
import donatekart from '../Img/donatekart.png';
import giveIndia from '../Img/giveIndia.png';
import paytm from '../Img/Paytm_logo.png';
import milaap from '../Img/milaap.png';
import unhcr from '../Img/unhcr3.PNG';
import unicef from '../Img/Unicef_logo_blue.png';
import AboutModal from './Modals'
function extra (props) {

  return (  <div>
        <AboutModal title={props.title}/>
    <a href={props.link}>
      <Button color="green">
        Visit/Donate
      </Button>
    </a>
  </div>
);
}



