{/* <img src={donatekart} alt="logo" /> */}
const PaytmCard= () => (
<Card>
      <Card.Content>
        <Image
          // floated="left"
          // size="mini"
          src={paytm}
        />
        <Card.Header>paytm</Card.Header>
        <Card.Meta>Helping Hand</Card.Meta>
        <Card.Description>
          While we are all familiar with paytm as an e-commerce payment system
          and digital wallet company. It has a portal exclusively for donating
          to NGOs, charities and relief funds.
        </Card.Description>
      </Card.Content>
      <Card.Content extra>{extra({title:"paytm: Helping hand",link:"https://paytm.com/helpinghand"})}</Card.Content>
    </Card>
)
const DonateCartCard = () => (
<Card
      image={donatekart}
      // image="https://www.donatekart.com/images/logo.png"
      header="Donatekart"
      meta="Delivering Happiness"
      description="DonateKart is one of it kind platform in India which is transforming
      the way people give to charities with the help of e-commerce. Through DonateKart, NGOs display the products they are in immediate need of. "
      extra={extra({title:"Donatekart",link:"https://www.donatekart.com/"})}
    >
      {/* <Card.Content image="https://www.donatekart.com/images/logo.png" /> */}
    </Card>
)

const GiveIndiaCard = () => (
  <Card
  image={giveIndia}
  // image="https://upload.wikimedia.org/wikipedia/commons/b/b5/Giveindia-a.svg"
  header="GiveIndia"
  meta="Friend"
  description="GiveIndia is a giving platform that allows you to support a cause of your choice from the NGOs that have been evaluated for transparency and credibility.

  GiveIndia is India's largest and most trustworthy giving platform for donors."
  extra={extra({title:"GiveIndia",link:"https://www.giveindia.org/"})}
>
  {/* <Card.Content image="https://www.donatekart.com/images/logo.png" /> */}
</Card>
)

const MilaapCard = () => (
  <Card
  image={milaap}
  header="Milaap"
  meta="Crowdfunding for India"
  description="Milaap is an online crowdfunding platform that enables anyone across India to raise funds for healthcare, education, sports, disaster relief and other personal causes, with great ease. Founded in 2010 by a team of young and passionate entrepreneurs, Milaap has pioneered the development of person to person giving in India."
  extra={extra({title:"Milaap",link:"https://milaap.org/"})}
>
  {/* <Card.Content image="https://www.donatekart.com/images/logo.png" /> */}
</Card>
)

const UN1 = () => (    <Card
  image={unicef}
  header="UNICEF"
  meta="For every child"
  description="Every child deserves a future, a fair chance and a childhood. That’s why UNICEF is here. We won’t give up until every child has a fair chance at a safe and happy life."
  extra={extra({title:"UNICEF: for every child",link:"https://www.donatetounicef.in/Rishita.aspx"})}
>
  {/* <Card.Content image="https://www.donatekart.com/images/logo.png" /> */}
</Card>)

const UN2 = () => (
  <Card
  image={unhcr}
  header="UNHCR"
  meta="The UN refugee agency"
  description="UNHCR, the UN Refugee Agency, is a global organization dedicated to saving lives, protecting rights and building a better future for refugees, forcibly displaced communities and stateless people."
  extra={extra({title:"UNHCR: UN refugee agency",link:"https://donate.unhcr.org/in/general/~my-donation?gclid=Cj0KCQjwwIPrBRCJARIsAFlVT89zsgkcvatDD6mzS05ElwCd8irkN1Df0Gx-6duxZq4d5dLTctpB6rYaApGEEALw_wcB&gclsrc=aw.ds"})}
>
  {/* <Card.Content image="https://www.donatekart.com/images/logo.png" /> */}
</Card>
)

