import React, { Component } from "react";
import { Button, Modal } from "semantic-ui-react";

class CopyrightModal extends Component {
  state = { open: false };

  closeConfigShow = (closeOnEscape, closeOnDimmerClick) => () => {
    this.setState({ closeOnEscape, closeOnDimmerClick, open: true });
  };

  close = () => this.setState({ open: false });

  render() {
    const { open, closeOnEscape, closeOnDimmerClick } = this.state;

    return (
      <div style={{paddingBottom:"25px", textAlign:"center", backgroundColor: "#282c34", color:"white"}}>
        <Button primary onClick={this.closeConfigShow(true, true)}>
        &copy; 2019 Copyright(Author's section)
        </Button>
        <Modal
          open={open}
          closeOnEscape={closeOnEscape}
          closeOnDimmerClick={closeOnDimmerClick}
          onClose={this.close}
          closeIcon
        >
          <Modal.Header>Copyright</Modal.Header>
          <Modal.Content>
            <i>This website was put together using the various suggestions, examples and open source code online.</i>
            (stackoverflow and github)
            <br/>
            The description of the websites are derived from their About/info section.<br/>  
            The list of websites displayed here are not exhaustive. These seemed to be the most popular and reliable ones whose primary task is to provide a payment gateway to donate to the charities.<br/>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={this.close} color="red">
              Close
            </Button>
          </Modal.Actions>
        </Modal>
      </div>
    );
  }
}

export default CopyrightModal;
