import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {AllCardGroup, NGOGroup, CrowdFundingGroup, UNRelatedGroup} from './Cards';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <Typography
            color="secondary"
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`scrollable-auto-tabpanel-${index}`}
            aria-labelledby={`scrollable-auto-tab-${index}`}
            {...other}
        >
            <Box p={3}>{children}</Box>
        </Typography>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `scrollable-auto-tab-${index}`,
        'aria-controls': `scrollable-auto-tabpanel-${index}`,
    };
}

const useStyles = makeStyles(theme => ({
    root: {
        width: '90%',
    },
}));

const StyledTab = withStyles(theme => ({
    root: {
        textTransform: 'none',
        color: '#fff',
        fontWeight: theme.typography.fontWeightBold,
        fontSize: theme.typography.pxToRem(15),
        marginRight: theme.spacing(1),
        '&:focus': {
            opacity: 1,
        },
    },
}))(props => <Tab disableRipple {...props} />);

const StyledTabs = withStyles({
    indicator: {
        display: 'flex',
        justifyContent: 'center',
        '& > div': {
            maxWidth: 40,
            width: '100%',
        },
    },
})(props => <Tabs {...props} TabIndicatorProps={{ children: <div /> }} />);


export default function ScrollableTabsButtonAuto() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);

    function handleChange(event, newValue) {
        setValue(newValue);
    }

    return (
        <div className={classes.root}>
            {/* <div> */}
            {/* <AppBar position="static" > */}
            <div 

            >
                <StyledTabs
                    value={value}
                    onChange={handleChange}
                      indicatorColor="primary"
                      textColor="primary"
                      variant="fullWidth"
                    // centered
                    scrollButtons="auto"
                    aria-label="scrollable auto tabs example"
                >
                    <StyledTab label="All" {...a11yProps(0)} />
                    <StyledTab label="NGOs" {...a11yProps(1)} />
                    <StyledTab label="Crowd Funding" {...a11yProps(2)} />
                    <StyledTab label="United Nations" {...a11yProps(3)} />

                </StyledTabs>
            </div>
            {/* </AppBar> */}
            <div style={{ alignItems:"center", justifyContent:"center"}}>
            <TabPanel value={value} index={0}>
            <AllCardGroup/>
      </TabPanel>
            <TabPanel value={value} index={1}>
                <NGOGroup/>
      </TabPanel>
            <TabPanel value={value} index={2}>
                <CrowdFundingGroup/>
      </TabPanel>
            <TabPanel value={value} index={3}>
        <UNRelatedGroup/>
      </TabPanel>
      </div>

        </div>
    );
}
